import React, { Fragment } from 'react';
import HelloReact from './js/components/containers/HelloReact';
import CreateElement from './js/components/containers/React.createElement';
import ReactComponent from './js/components/containers/React.Component';
import ReactPureComponent from './js/components/containers/React.PureComponent';
import FunctionalComponent from './js/components/containers/FunctionalComponent';

const App = () => {
  return (
    <Fragment>
      <HelloReact />
      <CreateElement title='I am the React.createElement component' />
      <ReactComponent />
      <ReactPureComponent title='I am the React.PureComponent component' />
      <FunctionalComponent />
    </Fragment>
  )
}

export default App;
